






















































.todo-list{
    font-size: 1.5rem;
    margin-top:0.5rem;
}
.todo-list li{
  position: relative;
  padding-right:20px;
} 
.hover-todo{
  background: #e5e5e5;
}
.todo-list input[type="checkbox"] {
    position: absolute;
    clip: rect(0,0,0,0);
}

.todo-list input[type="checkbox"] + label::before {
    content: '\a0';
    font-size: 2rem;
    display: inline-block;
    vertical-align: .2rem;
    width: 1.25rem;
    height: 1.25rem;
    margin-right: 1rem;
    border-radius: .2rem;
    background: #fff;
    text-indent: .15rem;
    line-height: .65;
    box-shadow: 0 0 .1rem .1rem #CD5B45;
}

.todo-list input[type="checkbox"]:checked + label::before {
    content: '\2713';
    background: #CD3333;
    color:black;
}

.todo-list input[type="checkbox"]:checked + label {
    text-decoration: line-through;
    color: #aaa;
}
.stagger-transition {
        -webkit-transition: all .3s ease;
        transition: all .3s ease;
    }
.stagger-enter,
.stagger-leave {
        opacity: 0;
    }
.remove {
  position :absolute;
  right:6px;
  top : calc(50% - 10px);
}



